  .container > * {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .container {
    width: 80%;
    margin: 0 auto;
    margin-top: 200px;
    padding: 25px;
  }

  .layout-1{
    display: grid;
    grid-template-columns: 1fr;
  }

  .col{
    text-align: center;
  }

  form{
    position: absolute;
    top: 200px;
    left: 50%;
    margin-top: -100px;
    margin-left: -250px;
    width: 500px;
    height: 200px;
  }
  form p{
    top: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    line-height: 170px;
    color: #ffffff;
    font-family: Arial;
  }
  form .fileInput{
    position: absolute;
    left: 0px;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    outline: none;
    cursor: pointer;
    color: transparent;
    background: transparent;
    border: 4px dashed rgba(255, 255, 255, 0.733);
    border-radius: 10px;
    
  }
  form .fileInput::-webkit-file-upload-button{
    opacity: 0;
  }
  form .fileInput:hover{
    border: 4px dashed rgba(255, 255, 255, 0.349);
  }
  form .dcinput{
    background-color: azure;
  }
  form button{
    color: #fff;
    background: #16a085;
    border: none;
    width: 100%;
    height: 35px;
    margin-left: -4px;
    border-radius: 4px;
    border-bottom: 4px solid #117A60;
    transition: all .2s ease;
    outline: none;
  }
  form button:hover{
    background: #149174;
      color: #0C5645;
  }
  form button:active{
    border:0;
  }
  
